csui.define('csui/models/facettopic',[
  'csui/lib/backbone'
], function (Backbone) {
  'use strict';

  var FacetTopicModel = Backbone.Model.extend({
    idAttribute: 'value',

    constructor: function FacetTopicModel() {
      Backbone.Model.prototype.constructor.apply(this, arguments);
    }
  });

  return FacetTopicModel;
});

